import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
import router from '@/router'

const vm = new Vue
const instance = axios.create({
	//baseURL: 'http://39.104.19.127:8080',
	baseURL:'http://192.168.1.78/',
})

//
let _loading;
Vue.prototype.showLoad = function (obj){
  var obj = obj||'';
  _loading = this.$loading({
    lock: true,
    text:obj,
    spinner:"el-icon-loading",
    background:"rgba(0, 0, 0, 0.8)"
  })
};
//
Vue.prototype.showMsg = function (obj,type,fun){
  type = type||1;
  if(type==1){//
    this.$message({ message: obj,type: 'success'});
  }else if(type==2){//
    this.$message.error({ message: obj});
  } 
  setTimeout(fun,500);
};

//
instance.interceptors.response.use(response => {
	console.log(response)
  if (response.data.code == '201') {
    window.sessionStorage.clear();
    Vue.prototype.showMsg(response.data.msg,'2',function(){
      router.push('/');
    });
    console.log(response.data.httpStatusCode)

	}else{
  	  setTimeout(function(){
      _loading&&_loading.close();
    },1000)
    return response
  }
}, err => {
  if (err && err.response) {
    setTimeout(function(){
      _loading&&_loading.close();
    },1000)
    switch (err.response.status) {
      case 400:
      console.log('');
      break;
      case 401:
      console.log('');
      break;
      case 403:
      console.log('');
      break;
      case 404:
      console.log(',');
      break;
      case 405:
      console.log('');
      break;
      case 408:
      console.log('');
      break;
      case 500:
      console.log('');
      break;
      case 501:
      console.log('');
      break;
      case 502:
      console.log('');
      break;
      case 503:
      console.log('');
      break;
      case 504:
      console.log('');
      break;
      case 505:
      console.log('http');
      break;
      default:
      console.log(`${err.response.status}`)
    }
  } else {
    console.log('')
  }
  return Promise.resolve(err.response)
});

export default instance





